        showSetPointImgDialog: false,
        pointBannerImgUrl: '',

        radioDefaultVal: 1, // 区域商品销量扩大系数配置 默认系数
        showSetRadioDialog: false,
        radioId: ''
      }
    },
    created() {
    },
    mounted() {
      // 获取表格中的数据
      this.doSearchHandel()
    },
    components: {
      "set-img": SetGoodsDetailImg,
      "set-radio": SetRadioDialog
    },
    methods: {
      /**
       * [doSearchHandel 获取运费信息]
       * @return {[type]} [description]
       */
      doSearchHandel() {
        utils.showLoading()
        this.$http.post(setFreight.freightList, { ...this.searchContent }).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.shopFreight) {
              let shopFreight = res.data.shopFreight
              this.freightMesg.id = shopFreight.id
              this.freightMesg.freight = shopFreight.freight && shopFreight.freight != 0 ? parseFloat(shopFreight.freight).toFixed(2) : '0.00'
              this.freightMesg.upSendPrice = shopFreight.upSendPrice && shopFreight.upSendPrice != 0 ? parseFloat(shopFreight.upSendPrice).toFixed(2) : '0.00'
              this.freightMesg.singlePrice = shopFreight.singlePrice && shopFreight.singlePrice != 0 ? parseFloat(shopFreight.singlePrice).toFixed(2) : '0.00'
            }

            if(res.data && res.data.shopRefundConfig) {
              let shopRefundConfig = res.data.shopRefundConfig
              this.refundMesg.id = shopRefundConfig.id
              this.refundMesg.lowAmount = shopRefundConfig.lowAmount && shopRefundConfig.lowAmount != 0 ? parseFloat(shopRefundConfig.lowAmount).toFixed(2) : '0.00'
            }

            if(res.data && res.data.shopRegionConfigVo) {
              let shopRegionConfigVoTemp = res.data.shopRegionConfigVo
              this.shopRegionConfigVo.defaultVal = shopRegionConfigVoTemp.defaultVal
              if(shopRegionConfigVoTemp.defaultVal) {
                this.shopRegionConfigVo.defaultValArr = shopRegionConfigVoTemp.defaultVal.split(',')
              }else {
                this.shopRegionConfigVo.defaultValArr = []
              }
              this.shopRegionConfigVo.id = shopRegionConfigVoTemp.id || null
            }

            if(res.data && res.data.leaderFilterConfigVo) {
              let leaderFilterConfigVoTemp = res.data.leaderFilterConfigVo
              this.leaderFilterConfigVo = {...leaderFilterConfigVoTemp}
              this.setAutomaticParams = {...leaderFilterConfigVoTemp}
              this.setAutomaticParams.businessType = (this.setAutomaticParams.businessType == 2 ? '2': '1')
              if(leaderFilterConfigVoTemp.notifyVoList && leaderFilterConfigVoTemp.notifyVoList.length) {
                let emails = []
                leaderFilterConfigVoTemp.notifyVoList.forEach(item => {
                  if(item.role == 1) {
                    this.customerService = {...item}
                  }else if(item.role == 2) {
                    this.operations = {...item}
                  }else if(item.role === 10) {
                    emails.push(item.email)
                  }
                })
                this.emails = emails.join(',')
              }
            }

            let phonesList = res.data && res.data.summarizeNotifyVo && res.data.summarizeNotifyVo.notifyVoList && res.data.summarizeNotifyVo.notifyVoList.length ? res.data.summarizeNotifyVo.notifyVoList : []
            this.phones = phonesList.map(item => {
              return item.phone
            }).join(',')
            let storeKMConfigVo = res.data && res.data.storeKMConfigVo ? res.data.storeKMConfigVo : {}
            this.saveSetKmId = storeKMConfigVo.id ? storeKMConfigVo.id : ''
            this.defaultVal = storeKMConfigVo.defaultVal ? storeKMConfigVo.defaultVal : 10

            let savingMoneyGiftVo = res.data && res.data.savingMoneyGiftVo ? res.data.savingMoneyGiftVo : {}
            this.couponId = savingMoneyGiftVo.id || ''
            this.bannerImgUrl = savingMoneyGiftVo.regionSavingMoneyGiftPic && savingMoneyGiftVo.regionSavingMoneyGiftPic.bannerImgUrl ? savingMoneyGiftVo.regionSavingMoneyGiftPic.bannerImgUrl : ''
            this.detailBackgroundImgUrl = savingMoneyGiftVo.regionSavingMoneyGiftPic && savingMoneyGiftVo.regionSavingMoneyGiftPic.detailBackgroundImgUrl ? savingMoneyGiftVo.regionSavingMoneyGiftPic.detailBackgroundImgUrl : ''

            let savingMoneyPointVo = res.data && res.data.savingMoneyPointVo ? res.data.savingMoneyPointVo : {}
            this.couponId = savingMoneyPointVo.id || ''
            this.pointBannerImgUrl = savingMoneyPointVo.regionSavingMoneyGiftPic && savingMoneyPointVo.regionSavingMoneyGiftPic.bannerImgUrl ? savingMoneyPointVo.regionSavingMoneyGiftPic.bannerImgUrl : ''

            let cityGoodsSalesVo = res.data && res.data.cityGoodsSalesVo ? res.data.cityGoodsSalesVo : {}
            this.radioId = cityGoodsSalesVo.id || ''
            this.radioDefaultVal = cityGoodsSalesVo.defaultVal || cityGoodsSalesVo.extVal || 1
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [showSetDialog 调起运费设置框]
       * @return {[type]} [description]
       */
      showSetDialog() {
        this.dialogFormVisible = true
        this.setFreightParams.id = this.freightMesg.id
        this.setFreightParams.freight = this.freightMesg.freight
        this.setFreightParams.singlePrice = this.freightMesg.singlePrice
        this.setFreightParams.upSendPrice = this.freightMesg.upSendPrice
      },
      /**
       * [doSetFreightHandel 确认设置运费]
       * @return {[type]} [description]
       */
      doSetFreightHandel() {
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        if(!this.setFreightParams.upSendPrice) {
          this.$message("请输入起送金额")
          return
        }else {
          if(!salePriceReg.test(this.setFreightParams.upSendPrice)) {
            this.$message('金额必须是大于等于0，小数保留小数点后2位') 
            return
          }else {
            if(parseFloat(this.setFreightParams.upSendPrice) > 500) {
              this.$message('金额必须小于等于500') 
              return
            }
          }
        }
        if(!this.setFreightParams.singlePrice) {
          this.$message("请输入单笔订单满免运费金额")
          return
        }else {
          if(!salePriceReg.test(this.setFreightParams.singlePrice)) {
            this.$message('金额必须是大于等于0，小数保留小数点后2位') 
            return
          }else {
            if(parseFloat(this.setFreightParams.singlePrice) > 500) {
              this.$message('金额必须小于等于500') 
              return
            }
          }
        }
        if(!this.setFreightParams.freight) {
          this.$message("请输入运费")
          return
        }else {
          if(!salePriceReg.test(this.setFreightParams.freight)) {
            this.$message('金额必须是大于等于0，小数保留小数点后2位') 
            return
          }else {
            if(parseFloat(this.setFreightParams.freight) > 500) {
              this.$message('金额必须小于等于500') 
              return
            }
          }
        }
        this.isDisabled = true
        this.$http.post(setFreight.saveFreight, { ...this.setFreightParams }).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("运费设置成功")
            this.dialogFormVisible = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      showSetRefundDialog() {
        this.dialogFormVisibleRefund = true
        this.setRefundParams.id = this.refundMesg.id
        this.setRefundParams.lowAmount = this.refundMesg.lowAmount
      },
      /**
       * [doSetRefundHandel 确认设置差额]
       * @return {[type]} [description]
       */
      doSetRefundHandel() {
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        if(!this.setRefundParams.lowAmount) {
          this.$message("请输入最小差额退款金额")
          return
        }else {
          if(!salePriceReg.test(this.setRefundParams.lowAmount)) {
            this.$message('金额必须是大于等于0，小数保留小数点后2位') 
            return
          }
        }
        this.isDisabled = true
        this.$http.post(setFreight.saveRefundConfig, { ...this.setRefundParams }).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("差额退款设置成功")
            this.dialogFormVisibleRefund = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      showSetImgDialog() {
        let _params = {
          title: '设置商品详情页底图',
          defaultValArr: this.shopRegionConfigVo.defaultValArr,
          shopRegionConfigVo: this.shopRegionConfigVo
        }
        this.$refs.setGoodsDetailImg.setDefaultMesg(_params)
      },

      showAutomaticDialog() {
        this.dialogAutomatic = true
      },
      doSetAutomaticHandel() {
        let reg = /^[+]{0,1}(\d+)$/
        if(!this.setAutomaticParams.businessType) {
          this.$message("请选择业务类型")
          return
        }else if(this.setAutomaticParams.businessType == 1) {
          if(!this.setAutomaticParams.leaderFilterAmount) {
            this.$message("请输入最小销售金额")
            return
          }else if(!reg.test(this.setAutomaticParams.leaderFilterAmount)) {
            this.$message('金额必须是大于等于0的整数') 
            return
          }
        }else if(this.setAutomaticParams.businessType == 2) {
          if(!this.setAutomaticParams.leaderFilterOrder) {
            this.$message("请输入最小销售单量")
            return
          }else if(!reg.test(this.setAutomaticParams.leaderFilterOrder)) {
            this.$message('单量必须是大于等于0的整数') 
            return
          }
        }
        // if(!this.customerService.name) {
        //   this.$message("请输入客服姓名")
        //   return
        // }
        // if(!this.customerService.phone) {
        //   this.$message("请输入客服手机号")
        //   return
        // }
        // if(!this.customerService.email) {
        //   this.$message("请输入客服邮箱")
        //   return
        // }
        // if(!this.operations.name) {
        //   this.$message("请输入运营姓名")
        //   return
        // }
        // if(!this.operations.phone) {
        //   this.$message("请输入运营手机号")
        //   return
        // }
        // if(!this.operations.email) {
        //   this.$message("请输入运营邮箱")
        //   return
        // }
        let emails = this.emails ? this.emails.replace(/\s+/g,"").replace(/\，/g, ',').split(',') : []
        let notifyAddressReqsTemp = emails.map(item => {
          return {
            name: '其他',
            email: item,
            role: 10
          }
        })
        this.setAutomaticParams.notifyAddressReqs = [{...this.customerService}, {...this.operations}, ...notifyAddressReqsTemp]
        this.isDisabled = true
        this.$http.post(setFreight.saveLeaderFilter, { ...this.setAutomaticParams }).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("团长店自动要货条件配置成功")
            this.dialogAutomatic = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      showNoticeMsgDialog() {
        this.$http.get(setFreight.getBroadcastLatest).then(res => {
          if(res.code === 0) {
            this.noticeMsg = res.data && res.data.message ? res.data.message : ''
            this.dialogFormNotice = true
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      doSetNoticeMsgHandel() {
        this.isDisabled = true
        let params = {}
        if(this.noticeMsg) {
          params.message = this.noticeMsg
          this.doSetNoticeMsgHandelReal(params)
        }else {
          params.message = "emptydada"
          params.deleted = true
          this.$confirm('通知内容为空，团长端将不会显示广播！是否继续保存?', '温馨提示', {
            confirmButtonText: '继续',
            cancelButtonText: '关闭',
            type: 'warning'
          }).then(() => {
            this.doSetNoticeMsgHandelReal(params)
          }).catch(() => {
            this.isDisabled = false
            this.dialogFormNotice = false
          })
        }
      },
      doSetNoticeMsgHandelReal(params) {
        this.$http.post(setFreight.insertNewBroadcast, {...params}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("团长小广播设置成功")
            this.dialogFormNotice = false
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      showNoticePhoneDialog() {
        this.noticePhoneDialog = true
      },
      doSetNoticePhoneHandel() {
        let phones = this.phones ? this.phones.replace(/\s+/g,"").replace(/\，/g, ',').split(',') : []
        let notifyAddressReqsTemp = phones.map(item => {
          return {
            name: '其他',
            phone: item,
            role: 10
          }
        })
        this.$http.post(setFreight.saveSummarizeNotify, {notifyAddressReqs: notifyAddressReqsTemp}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("区域商品自动要货失败短信推送人员设置成功")
            this.noticePhoneDialog = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      /**
       * @description: 省钱礼包活动图片设置
       * @param {type} 
       * @return: 
       */
      showSetSavePackageImgDialog() {
        this.showSetSavePackImgDialog = true
      },
      /**
       * @description: 保存修改入口Banner和详情背景图
       * @param type {Boolean} true 使用默认的 false 不适用默认的图片
       * @return: 
       */
      doSaveChangeImgUrl(type) {
        let params = {
          id: this.couponId,
          key: 'savingMoneyGiftPics',
          type: 4,
          regionSavingMoneyGiftPic: {
            bannerImgUrl: type ? this.defaultBannerImgUrl : this.bannerImgUrl,
            detailBackgroundImgUrl: type ? this.defaultDetailBackgroundImgUrl : this.detailBackgroundImgUrl
          }
        }
        this.$http.post(setFreight.saveRegionConfig, {...params}).then(res => {
          if(res.code === 0) {
            this.showSetSavePackImgDialog = false
            this.$message.success("修改省钱礼包图片成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.showSetSavePackImgDialog = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      setCurrentType(type) {
        this.currentType = type
      },
      /**
       * @description: 使用默认图片
       * @param {type} 
       * @return: 
       */
      useDefaultImg() {
        const h = this.$createElement;
        this.$confirm('确定要使用默认图片吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doSaveChangeImgUrl(true)
        }).catch(() => {})
      },
      /**
       * [imageUploadReg 上传图片检测]
       * @param  {[type]} file [description]
       * @return {[type]}      [description]
       */
       imageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        // const isSizeNum = file.size <= 1 * 300 * 1024
        const isSizeNum = true
        if (!isPic || !isJPG || !isSizeNum) {
          if(this.currentType === 'bannerImgUrl') {
            this.$message.error('入口Banner建议上传大小不超过1M、尺寸为750*260px的png或jpg图片')
          }else if(this.currentType === 'detailBackgroundImgUrl') {
            this.$message.error('详情背景图建议上传大小不超过1M、尺寸为750*520px的png或jpg图片')
          }
        }
        return isPic && isJPG && isSizeNum
      },
      /**
       * [getToken 通过后台封装的接口获取七牛的token]
       * @return {[type]}            [description]
       */
       getToken () {
        return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.imageUploadLoading = false
          this.$notify.error({
            title: '获取七牛token提示',
            message: e.message
          })
        })
      },
      /**
       * [emitUpload 上传图片]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      emitUpload(e) {
        this.imageUploadLoading = true
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.imageUploadLoading = false
            },
            complete: (res) => {
              // 添加内容设置图片地址
              let urlImage = 'http://retailimg01.benlailife.com/' + res.key
              this.imageUploadLoading = false
              // 设置对应的图片地址
              if(this.currentType === 'bannerImgUrl') {
                this.bannerImgUrl = urlImage
              }else if(this.currentType === 'detailBackgroundImgUrl') {
                this.detailBackgroundImgUrl = urlImage
              }else if(this.currentType === 'pointBannerImgUrl') {
                this.pointBannerImgUrl = urlImage
              }
            },
            next: res => {
            }
          })
        })
      },

      /**
       * @description: 用户展示门店的最远距离
       * @param {type} 
       * @return: 
       */
      setRangeDialog() {
        this.exVal = this.defaultVal
        this.showSetRangeDialog = true
      },
      doSetRangeHandel() {
        if(!this.exVal && parseFloat(this.exVal) != 0) {
          this.$message(`请输入最远距离`)
          return
        }else {
          if(!salePriceReg.test(this.exVal) || parseFloat(this.exVal) == 0) {
            this.$message('最远距离是大于0，小数保留小数点后2位') 
            return
          }
        }
        this.isDisabled = true
        this.$http.post(setFreight.saveStoreKMConfig, {id: this.saveSetKmId, defaultVal: this.exVal}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("用户展示门店的最远距离设置成功")
            this.showSetRangeDialog = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      showSetPointImg() {
        this.showSetPointImgDialog = true
      },
      usePointDefaultImg() {
        this.$confirm('确定要使用默认图片吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doSaveChangePointImgUrl(true)
        }).catch(() => {})
      },
      doSaveChangePointImgUrl(type) {
        let params = {
          id: this.couponId,
          key: 'savingMoneyPointPics',
          type: 5,
          regionSavingMoneyGiftPic: {
            bannerImgUrl: type ? this.defaultPointBannerImgUrl : this.pointBannerImgUrl
          }
        }
        this.$http.post(setFreight.saveRegionConfig, {...params}).then(res => {
          if(res.code === 0) {
            this.showSetPointImgDialog = false
            this.$message.success("设置积分换券活动图片成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.showSetPointImgDialog = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      /**
       * @description: 设置区域商品销量扩大系数
       */
      setRadioDialog() {
        let params = {
          id: this.radioId || '',
          visible: true,
          defaultVal: this.radioDefaultVal
        }
        this.$refs.setRadio.setDefaultMsg(params)
      }

    }
  }
</script>


