<template>
  <el-dialog 
    title="区域商品销量扩大系数配置" 
    :visible.sync="showSetRadioDialog" 
    width='500px' 
    :close-on-click-modal="false" 
    :close-on-press-escape="false">
    <div>
      <span>销量 = 真实销量 x </span>
      <el-input class="search-input-mini-width" size="mini" v-model.trim="radioDefaultVal"></el-input>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="showSetRadioDialog = false" size="mini">取消</el-button>
      <el-button type="primary" size="mini" :disabled="isDisabled" @click="doSetRadioHandel">确定</el-button>
    </div>
  </el-dialog>
</template>
<script>
  import requestUrl from "@/api/MallConfig"
  const numReg = /^[1-9]*[1-9][0-9]*$/
  export default {
    data() {
      return {
        isDisabled: false,
        showSetRadioDialog: false,
        radioDefaultVal: 1,
        id: ''
      }
    },
    methods: {
      setDefaultMsg(params) {
        this.showSetRadioDialog = params.visible || false
        this.radioDefaultVal = params.defaultVal || 1
        this.id = params.id || ''
      },
      doSetRadioHandel() {
        if(!this.radioDefaultVal) {
          this.$message("销量扩大系数为大于0的正整数")
          return
        }else if(!numReg.test(this.radioDefaultVal)) {
          this.$message("销量扩大系数为大于0的正整数")
          return
        }
        let params = {
          id: this.id,
          key: 'cityGoodsSales',
          type: 6,
          defaultVal: this.radioDefaultVal || 1,
          extVal: this.radioDefaultVal || 1
        }
        this.isDisabled = true
        this.$http.post(requestUrl.saveRegionConfig, {...params}).then(res => {
          this.isDisabled = false
          if(res.code === 0) {
            this.showSetRadioDialog = false
            this.$message.success("区域商品销量扩大系数配置成功")
            this.$emit("doSearchHandle")
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.showSetRadioDialog = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    }
  }
</script>


