<style>
  @import "../../../../static/style/file_upload.css";
  .draggable-img-wrap {
    overflow: hidden;
    min-height: 110px;
  }
  .mh-up-w-draggable {
    float: left;
    margin: 0 5px;
  }
  .mh-up-d-w-btn-w {
    padding: 0 5px;
    line-height: 0;
    margin-top: 8px;
    margin-bottom: 8px;
    text-align: center;
  }
  .up-h-i {
    cursor: pointer;
    padding-left: 6px;
    padding-right: 6px;
  }
  .up-h-i:hover {
    color: #3080fe;
  }
</style>

<template>
  <div>
    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      :title="title" 
      :visible.sync="isShow" 
      width='524px'>
      <div class="draggable-img-wrap">
        <draggable v-model="imageListArr" @update="datadragEnd">
          <transition-group>
            <div class="mh-up-w-draggable" v-for="(item, idx) of imageListArr" @click="setItemIdx(idx)" :key="item.imgUrl">
              <el-upload
                class="avatar-uploader"
                action=""
                :show-file-list="false"
                :before-upload="imageUploadRegForMore"
                :http-request="emitUpload">
                <div v-loading="imageUploadLoading && currentUpFileIdx == idx">
                  <img v-if="item.imgUrl" :src="item.imgUrl" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </div>
              </el-upload>
              <div class="mh-up-d-w-btn-w" v-if="item.imgUrl && item.imgUrl != ''"> 
                <span class="up-h-i" @click="deleteUpFile(idx)"><i class="el-icon-delete"></i></span>
                <span class="up-h-i"><i class="el-icon-rank"></i></span>
              </div>
            </div>
          </transition-group>
        </draggable>
      </div>
      <p>建议上传宽度为750像素的JPG/PNG图片，大小不能超过1M，最多可上传3张</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="disabled" :loading="saveLoading">确定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import Draggable from 'vuedraggable'
import * as utils from '@/utils/utils'
import * as qiniu from 'qiniu-js'
import Storage from '@/utils/localStorage'
const fileUrlList = {
  getQiNiuToken: '/foundation/qiniu/auth/file'
}
const requestUrl = {
  // 区域商品售后说明图
  saveRegionConfig: '/plateform/a/mall/shopFreight/saveRegionConfig'
}
export default {
  data () {
    return {
      disabled: false,
      saveLoading: false,
      title: '设置商品详情页底图',
      isShow: false,

      currentUpFileIdx: -1, // 默认选中的文件上传框
      // 商品详情图加载
      imageUploadLoading: false,

      imageListArr: [],
      shopRegionConfigVo: {}
    }
  },
  components: {
    'draggable': Draggable
  },
  methods: {
    /**
     * [setDefaultMesg 设置弹框默认属性]
     * @param {[type]} _params [自定义参数]
     */
    setDefaultMesg (_params) {
      this.isShow = true
      if (_params.title) {
        this.title = _params.title
      }

      if(_params.shopRegionConfigVo) {
        this.shopRegionConfigVo = {..._params.shopRegionConfigVo}
      }

      if(_params.defaultValArr && _params.defaultValArr.length) {
        this.imageListArr = _params.defaultValArr.map((item, index) => {
          let imgListItem = {
            imgUrl: item,
            sortNum: index
          }
          return imgListItem
        })
      }else {
        this.imageListArr = []
      }
      this.imageListArr.push({"imgUrl": "", "sortNum": ""})
    },

    /**
     * [datadragEnd 拖拽结束]
     * @return {[type]} [description]
     */
    datadragEnd(evt) {
      for(let [index, elem] of new Map(this.imageListArr.map((item, i) => [i, item]))){
        elem.sortNum = index + 1
      }

      this.imageListArr = this.imageListArr.filter(item => {
        return item.imgUrl != ''
      });

      let imageListItem = {
        imgUrl: '',
        sortNum: ''
      }
      this.imageListArr.push(imageListItem)
    },
    /**
     * [emitUpload 上传图片]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    emitUpload(e) {
      if(this.imageListArr.length > 3 && this.currentUpFileIdx != -1) {
        this.$message.error('图片最多可上传3张')
        return
      }
      this.imageUploadLoading = true
      this.getToken().then((res) => {
        // 开始上传图片
        let uploader = qiniu.upload(e.file, res.fileName, res.token)
        uploader.subscribe({
          error: () => {
            this.imageUploadLoading = false
          },
          complete: (res) => {
            // 添加内容设置图片地址
            let urlImage = 'http://retailimg01.benlailife.com/' + res.key
            this.imageUploadLoading = false
            // 设置对应的图片地址
            this.setImgUrl(this.currentUpFileIdx, urlImage)
          },
          next: res => {
          }
        })
      })
    },
    /**
     * [setImgUrl 设置对应的图片路径]
     * @param {[type]} _idx    [description]
     * @param {[type]} _imgUrl [description]
     */
    setImgUrl(_idx, _imgUrl) {
      let urlImage = _imgUrl
      let currentIdx = _idx

      for(let [i, item] of new Map(this.imageListArr.map((item, i) => [i, item]))) {
        if(i === currentIdx) {
          this.imageListArr[i].imgUrl = urlImage
          this.imageListArr[i].sortNum = parseInt(currentIdx) + 1
        }
      }
      let hash = {}
      let imageListItem = {
        imgUrl: '',
        sortNum: ''
      }
      this.imageListArr.push(imageListItem)
      // 去重  防止多个空对象
      this.imageListArr = this.imageListArr.reduceRight((item, next) => {
        hash[next.imgUrl] ? '' : hash[next.imgUrl] = true && item.push(next)
        return item
      }, [])
      this.imageListArr.reverse()
    },
    /**
     * [imageUploadReg 上传图片检测]
     * @param  {[type]} file [description]
     * @return {[type]}      [description]
     */
    imageUploadRegForMore (file) {
      const isPic = file.type.indexOf('image/') !== -1
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isSizeNum = file.size <= 1 * 1024 * 1024
      if (!isPic) {
        this.$message.error('建议上传宽度为750像素的JPG/PNG图片，大小不能超过1M，最多可上传3张')
      }
      if (!isJPG) {
        this.$message.error('建议上传宽度为750像素的JPG/PNG图片，大小不能超过1M，最多可上传3张')
      }
      if (!isSizeNum) {
        this.$message.error('建议上传宽度为750像素的JPG/PNG图片，大小不能超过1M，最多可上传3张')
      }
      return isPic && isJPG && isSizeNum
    },
    /**
     * [getToken 通过后台封装的接口获取七牛的token]
     * @return {[type]}            [description]
     */
    getToken () {
      return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
        if (res.code == 0) {
          let token = res.data.token
          let fileName = res.data.key
          return {fileName, token}
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.imageUploadLoading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [setItemIdx 设置当前的上传文件的idx]
     * @param {[type]} _idx [description]
     */
    setItemIdx(_idx) {
      this.currentUpFileIdx = _idx
    },
    /**
     * [removeFileLeft 删除]
     * @param  {[type]} _idx [description]
     * @return {[type]}       [description]
     */
    deleteUpFile(_idx) {
      let idx = _idx
      this.imageListArr = this.imageListArr.filter((item, idx) => {
        return idx != _idx
      })
    },
    /**
     * [doConfirmHandel 点击确定]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      let imageListArrTemp = this.imageListArr.filter((item, idx) => {
        return item.imgUrl && item.imgUrl !== ''
      })
      let imageListArr = imageListArrTemp.map((item, idx) => {
        if(item.imgUrl && item.imgUrl !== '') {
          return item.imgUrl
        }
      })
      this.shopRegionConfigVo.defaultValArr = imageListArr
      this.shopRegionConfigVo.defaultVal = imageListArr.join(',')
      this.shopRegionConfigVo.type = 1
      this.shopRegionConfigVo.key = "saleServicePics"
      this.$http.post(requestUrl.saveRegionConfig, {...this.shopRegionConfigVo}).then(res => {
        // 保存按钮可再次点击
        setTimeout(() => {
          this.disabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.isShow = false
          this.$message.success("设置图片成功")
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        setTimeout(() => {
          this.disabled = false
          this.saveLoading = false
        }, 800)
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
      

    } 
  }
}
</script>



